import React from 'react'
import cn from 'classnames'
import './index.scss';
import Icon from '../../../Icon'
import { useApplication, useSwitchToken } from '../../../../state/application/hooks'
import { useList } from '../../../../state/list/hooks'

const MaketInfo: React.FC = () => {
    // console.log(useApplication())
    const { unitToken: { token, symbol } } = useApplication();
    const { CurrentSwapLiquidity } = useList();
    const { token: LiqToken, price, trade24h } = CurrentSwapLiquidity
    const setUnitToken = useSwitchToken()

    return (
        <div className='maket-info'>
            <div className="item">
                <Icon symbol="eth" />
                <div>
                    <span className='topSpan'>
                        {LiqToken.from}
                        <span className='to'> {LiqToken.to}</span>
                    </span>
                    <span className='bottomSpan'>
                        {token !== "ETH" && <i className='eth'>{symbol}</i>}
                        <i>{price.value}</i>
                        {token !== "USD" && <i className='eth'>{symbol}</i>}
                        <Icon symbol={price.float} size={14} />
                        <i className='up'>{price.parcent}</i>
                    </span>
                </div>
            </div>
            <div className="item">
                <div>
                    <span className='topSpan eth'>Transitions,24h</span>
                    <span className='bottomSpan'>
                        <i className='amount'>{trade24h}</i>
                        <Icon symbol='up' size={15} />
                        <i className='up'>10.2%</i>
                    </span>
                </div>
            </div>
            <button className={cn('swtich-token', { [token]: 1 })}
                onClick={setUnitToken}
            >
                <span>USD</span>
                <Icon symbol='switch' size={16} />
                <span>ETH</span>
            </button>
        </div>
    )
}
export default MaketInfo
