import React from "react";

const periods = ["1m", "5m", "30m", "1H", "6H", "12H", "1D"]
const TopBar: React.FC<{kLineChart: any, onFullScreen: () => void}> = ({kLineChart, onFullScreen}) => {

  return (
    <div className='kline-topBar'>
      <ul>
        {periods.map((item) => {
          return <li key={item}>{item}</li>
        })}
        <li onClick={onFullScreen}>全屏</li>
      </ul>
    </div>
  )
}

export default TopBar
