
import { createReducer } from '@reduxjs/toolkit'
import { setLiqiudityList, setLiquidity, setPoolList, setTradingList } from './actions'

export type ListType = Array<SwapLiquidityItem>

export interface SwapLiquidityItem {
    type: string,
    saved: boolean,
    token: {
        from: string,
        to: string
    },
    trade24h: string,
    liquidityAmount: string,
    price: {
        value: string | number,
        float: string,
        parcent: string
    }
}

export interface TokenType {
    amount: string | number,
    token: string
}

export interface PoolTradeItemType {
    trxid: string,
    sign: string,
    from: TokenType,
    to: TokenType,
    eqTokenValue: TokenType,
    time: string | number
}

export type PoolTradeListType = Array<PoolTradeItemType>

export interface ListState {
    readonly SwapLiquidityList: ListType
    readonly CurrentSwapLiquidity: SwapLiquidityItem
    readonly PoolList: PoolTradeListType
    readonly TradeList: PoolTradeListType
}

const initialState: ListState = {
    PoolList: [],
    TradeList: [],
    SwapLiquidityList: [],
    CurrentSwapLiquidity: {
        type: "wallet",
        saved: false,
        token: {
            from: "OKEX",
            to: "BSC",
        },
        trade24h: "2232.23",
        liquidityAmount: "123232.12",
        price: {
            value: "12.1",
            float: "up",
            parcent: "12%",
        },
    },
}

export default createReducer(initialState, builder =>
    builder
        .addCase(setLiqiudityList, (state, { payload }) => {
            if (payload && Array.isArray(payload)) {
                state.SwapLiquidityList = payload.slice(0)
            }
        })
        .addCase(setLiquidity, (state, { payload }) => {
            state.CurrentSwapLiquidity = payload
        })
        .addCase(setPoolList, (state, { payload }) => {
            state.PoolList = payload
        })
        .addCase(setTradingList, (state, { payload }) => {
            state.TradeList = payload
        })
)
