import React, {useState} from "react";
import './index.scss'
import LineChart from "../LineChart";
import { useApplication } from '../../../../state/application/hooks'
import cn from "classnames";

interface Props {
  type: 'trade' | 'liquidity'
}

const TradeHeader: React.FC<Props> = ({ type }) => {
  const { unitToken } = useApplication()
  const [isAllTime, setIsAllTime] = useState(false)
  const { title, color } = (() => {
    let title = ''
    let color = 'white'
    switch (type) {
      case 'liquidity':
        title = 'Liquidity'
        color = '#2273f4'
        break
      case 'trade':
        title = 'Trading Volume, 24h'
        color = '#7e73f4'
        break
    }

    return { title, color }
  })()

  const isETH = unitToken.token === "ETH"

  return (
    <div className='c-trade-header'>
      <p className='title'>{title}</p>
      <div className='amount'>
        <p>
          {!isETH && <i>$</i>}
          8,403,237
          {isETH && <i style={{ fontSize: '24px' }}>ETH</i>}
        </p>
        <p>↑1.2%</p>
      </div>
      <div className='tab-btn'>
        <button className={cn({'active': !isAllTime})} onClick={() => {setIsAllTime(false)}}>1 Week</button>
        <button className={cn({'active': isAllTime})} onClick={() => {setIsAllTime(true)}}>All Time</button>
      </div>
      <LineChart color={color} data={isAllTime ? [
        { date: '21 Mar', value: 2122 },
        { date: '22 Mar', value: 3172 },
        { date: '23 Mar', value: 1922 },
        { date: '25 Mar', value: 2865 },
        { date: '26 Mar', value: 3298 },
        { date: '27 Mar', value: 4510 },
      ] : [
        { date: '21 Mar', value: 1001 },
        { date: '22 Mar', value: 1203 },
        { date: '23 Mar', value: 1901 },
        { date: '25 Mar', value: 2010 },
        { date: '26 Mar', value: 1790 },
        { date: '27 Mar', value: 2201 },
      ]} keys={['date', 'value']} />
    </div>
  )
}

export default TradeHeader;
