import { createAction } from '@reduxjs/toolkit'
import { SwapLiquidityItem, PoolTradeListType } from './reducer'

export const setLiqiudityList = createAction<Array<SwapLiquidityItem>>('application/setLiqiudityList')
export const setLiquidity = createAction<SwapLiquidityItem>('application/setLiquidity')
export const setPoolList = createAction<PoolTradeListType>('application/setPoolList')
export const setTradingList = createAction<PoolTradeListType>('application/setTradingList')