import { useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { AppDispatch, AppState, } from '../index'
import { UnitToken, ApplicationState } from './reducer'
import { setUnitToken, } from './actions'

export function useApplication(): ApplicationState {
    return useSelector((state: AppState) => state.application)
}

export function useSwitchToken(): () => void {
    const dispatch = useDispatch<AppDispatch>()
    return useCallback(() => dispatch(setUnitToken()), [dispatch])
}