import React, {
    useState,
    useMemo
} from 'react'
import './index.scss';
import { TokenSearch, } from '../../../components'
import SwapList from '../component/SwapList'
import { useApplication } from '../../../state/application/hooks'
import { useList, useSetLiquidity } from '../../../state/list/hooks'

import cn from 'classnames';

const Buttons = [
    { c: "rank", text: "Top Volume" },
    { c: "wallet", text: "Wallet" },
    { c: "like", text: "Favtorite" },
]

const SwapTokenList: React.FC = () => {
    const [condition, setCondition] = useState<string>('rank')
    const { unitToken } = useApplication()
    const { SwapLiquidityList } = useList()

    const list = useMemo(() => {
        if (condition === 'like') {
            return SwapLiquidityList.filter(item => item.saved)
        }
        return SwapLiquidityList.filter(item => item.type === condition)
    }, [condition, SwapLiquidityList])

    const setLiquidity = useSetLiquidity()

    return (
        <div className='swaptoken-list'>
            <TokenSearch />
            <div className="swaptoken-tag">
                {
                    Buttons.map(({ c, text }) => {
                        return (
                            <button
                                className={cn({ active: c === condition })}
                                key={c} onClick={() => setCondition(c)}>{text}</button>
                        )
                    })
                }
            </div>
            <SwapList data={list} unitToken={unitToken} setLiquidity={setLiquidity} />
        </div >
    )
}

export default SwapTokenList
